@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

/* Global Styles */
:root {
    --primary-color: #800202;
    --dark-color: #000000;
    --light-color: #3d0000;
    --danger-color: #dc3545;
    --success-color: #28a745;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    line-height: 1.6;
    background-color: #3d0000;
    color: rgb(255, 255, 255);
    justify-content: center;
    text-align: center;
}

a {
    color: #800202;
    color: var(--primary-color);
    text-decoration: none;
}

a:hover {
    color: #666;
}

/* desktop styles////////////////////////////////////////////////////////////////////////////////////////////////////////////////////// */
@media (min-width: 701px) {
    .brand-name {
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
        font-size: 105px;
        transform: rotate(180deg);
        font-weight: bolder;
        margin: auto;
        padding: 10px;
        float: left;
        max-width: -webkit-fit-content;
        max-width: -moz-fit-content;
        max-width: fit-content;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        color: rgb(172, 54, 54);
        text-decoration: overline;
        z-index: 0;
    }
    .brand-name:hover {
        color: rgb(235, 95, 95);
    }
    .brand-and-nav {
        position: -webkit-sticky;
        position: sticky;
        top: 0;
    }

    ul {
        list-style: none;
    }

    img {
        width: 100%;
    }

    /* Utilities */
    .container {
        max-width: 1100px;
        margin: auto;
        overflow: hidden;
        padding: 0 2rem;
        justify-content: center;
        text-align: center;
    }
    .user-container {
        max-width: 1100px;
        margin: auto;
        overflow: hidden;
        padding: 20px;
        overflow-y: auto;
        -ms-scroll-chaining: translate(0, 20px);
            overscroll-behavior: translate(0, 20px);
        justify-content: center;
        text-align: center;
    }
    .about-container {
        margin: auto;
        display: inline-block;
        padding: 20px;

        -ms-scroll-chaining: translate(0, 20px);

            overscroll-behavior: translate(0, 20px);
        justify-content: center;
        text-align: center;
    }

    /* Text Styles*/
    .x-large {
        font-size: 4rem;
        line-height: 1.2;
        margin-bottom: 1rem;
    }

    .large {
        font-size: 3rem;
        line-height: 1.2;
        margin-bottom: 1rem;
    }

    .lead {
        font-size: 1.5rem;
        margin-bottom: 1rem;
    }

    .text-center {
        text-align: center;
    }

    .text-primary {
        color: #800202;
        color: var(--primary-color);
    }

    .text-dark {
        color: #000000;
        color: var(--dark-color);
    }

    .text-success {
        color: #28a745;
        color: var(--success-color);
    }

    .text-danger {
        color: #dc3545;
        color: var(--danger-color);
    }

    .text-center {
        text-align: center;
    }

    .text-right {
        text-align: right;
    }

    .text-left {
        text-align: left;
    }

    /* Center All */
    .all-center {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin: auto;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    /* Cards */
    .card {
        animation-name: floating;
        animation-duration: 3s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
        padding: 1rem;
        border: rgb(186, 169, 169) 1px solid;
        margin: 0.4rem 0;
        border-radius: 10px;
        overflow: hidden;
    }
    .card:hover {
        background-color: rgba(248, 126, 126, 0.469);
        animation-name: floating;
        animation-duration: 3s;
        animation-iteration-count: 1;
        animation-timing-function: ease-in-out;
    }
    @keyframes floating {
        0% {
            transform: translate(0, 0px);
        }
        50% {
            transform: translate(0, 10px);
        }
        100% {
            transform: translate(0, 0px);
        }
    }
    .username {
        font-size: large;
    }
    /* List */
    .list {
        margin: 0.5rem 0;
    }

    .list li {
        padding-bottom: 0.3rem;
    }

    /* Padding */
    .p {
        padding: 0.5rem;
    }
    .p-1 {
        padding: 1rem;
    }
    .p-2 {
        padding: 2rem;
    }
    .p-3 {
        padding: 3rem;
    }
    .py {
        padding: 0.5rem 0;
    }
    .py-1 {
        padding: 1rem 0;
    }
    .py-2 {
        padding: 2rem 0;
    }
    .py-3 {
        padding: 3rem 0;
    }

    /* Margin */
    .m {
        margin: 0.5rem;
    }
    .m-1 {
        margin: 1rem;
    }
    .m-2 {
        margin: 2rem;
    }
    .m-3 {
        margin: 3rem;
    }
    .my {
        margin: 0.5rem 0;
    }
    .my-1 {
        margin: 5px 0;
    }
    .my-2 {
        margin: 2rem 0;
    }
    .my-3 {
        margin: 30px;
    }
    .user-card-left {
        float: left;
        padding: 10px;
        margin: 0;
        border-radius: 10px;
        overflow-y: hidden;
        border: 2px white solid;
        max-width: 300px;
        max-height: 79vh;
    }
    .user-card-right {
        padding: 10px;
        margin: auto;
        border-radius: 10px;
        overflow-y: auto;
        scroll-behavior: smooth;
        border: 2px white solid;
        max-width: 700px;
        max-height: 79vh;
        text-align: left;
    }
    .about-card {
        padding: 10px;

        margin: auto;
        margin-bottom: 10px;
        border-radius: 10px;
        overflow-y: auto;
        scroll-behavior: smooth;

        max-width: 900px;
        text-align: left;
    }

    /* Grid */
    .grid-2 {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0.8rem;
    }

    .grid-3 {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 1rem;
    }

    .grid-4 {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 1rem;
    }

    .btn {
        display: inline-block;
        background: palevioletred;
        color: #333;
        padding: 0.4rem 1.3rem;
        font-size: 1rem;
        border: none;
        border-radius: 10px;
        cursor: pointer;
        margin-right: 0.5rem;
        transition: opacity 0.2s ease-in;
        border: 1px solid white;
    }

    .btn-link {
        background: none;
        padding: 0;
        margin: 0;
    }

    .btn-block {
        display: block;
        width: 100%;
    }

    .btn-sm {
        font-size: 0.8rem;
        padding: 0.3rem 1rem;
        margin-right: 0.2rem;
    }

    .badge {
        display: inline-block;
        font-size: 0.8rem;
        padding: 0.2rem 0.7rem;
        text-align: center;
        margin: 0.3rem;
        background: #3d0000;
        background: var(--light-color);
        color: #333;
        border-radius: 5px;
        border: 1px solid white;
    }

    .alert {
        padding: 0.7rem;
        margin: 1rem 0;
        opacity: 0.9;
        background: #3d0000;
        background: var(--light-color);
        color: #333;
    }

    .btn-primary,
    .bg-primary,
    .badge-primary,
    .alert-primary {
        color: blue;
    }

    .btn-light,
    .bg-light,
    .badge-light,
    .alert-light {
        background: #3d0000;
        background: var(--light-color);
        color: rgb(233, 225, 225);
    }

    .btn-dark,
    .bg-dark,
    .badge-dark,
    .alert-dark {
        background: #000000;
        background: var(--dark-color);
        color: #fff;
    }

    .btn-danger,
    .bg-danger,
    .badge-danger,
    .alert-danger {
        background: #dc3545;
        background: var(--danger-color);
        color: #fff;
    }

    .btn-success,
    .bg-success,
    .badge-success,
    .alert-success {
        background: #28a745;
        background: var(--success-color);
        color: #fff;
    }

    .btn-white,
    .bg-white,
    .badge-white,
    .alert-white {
        background: #fff;
        color: #333;
        border: #ccc solid 1px;
    }

    .btn:hover {
        opacity: 0.8;
    }
    .back2home {
        max-width: -webkit-fit-content;
        max-width: -moz-fit-content;
        max-width: fit-content;
        max-height: -webkit-fit-content;
        max-height: -moz-fit-content;
        max-height: fit-content;
        color: rgb(255, 255, 255);
        justify-content: center;
        text-align: center;
        display: inline-block;
        background-color: #a44c4c8e;
        margin: auto;
        padding: 2px 20px 0px 20px;
        border-radius: 10px;
        border: 2px solid rgb(142, 110, 110);
    }
    .bg-light,
    .badge-light {
        border: #ccc solid 1px;
    }

    .round-img {
        border-radius: 50%;
    }

    /* Forms */
    input {
        margin: 1.2rem 0;
    }
    #alertmsg {
        justify-content: left;
        text-align: left;
        background: transparent;
    }
    .search-console {
        float: left;
        max-width: -webkit-fit-content;
        max-width: -moz-fit-content;
        max-width: fit-content;
        padding: 5px;
        border: 2px solid rgb(142, 110, 110);
        border-radius: 10px;
        justify-content: center;
        text-align: center;
    }
    .search-console:hover {
        background: rgba(245, 104, 104, 0.469);
        border: 2px solid white;
    }
    #username-input {
        border-radius: 10px;
    }

    .form-text {
        display: block;
        margin-top: 0.3rem;
        color: #888;
    }

    input[type='text'],
    input[type='email'],
    input[type='password'],
    input[type='date'],
    select,
    textarea {
        display: block;
        width: 100%;
        padding: 0.4rem;
        font-size: 1.2rem;
        border: 1px solid #ccc;
    }

    input[type='submit'],
    button {
        font: inherit;
    }

    table th,
    table td {
        padding: 1rem;
        text-align: left;
    }

    table th {
        background: #3d0000;
        background: var(--light-color);
    }

    /* Navbar */
    .navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0.7rem 2rem;
        z-index: 1;
        width: 100%;
        opacity: 0.9;
        margin-bottom: 1rem;
        background-image: linear-gradient(
            to right,
            #800202,
            rgba(175, 102, 102, 0.088) 30%
        );
        background-image: linear-gradient(
            to right,
            var(--primary-color),
            rgba(175, 102, 102, 0.088) 30%
        );
    }
    #navbar-icon {
        margin-right: 10px;
        font-size: 40px;
    }
    .navbar-content {
        display: flex;
    }
    .navbar-btn {
        color: #fff;
        margin: 0px 0px 0px 7px;
        padding: 2px 20px 0px 20px;
        border-radius: 10px;
        border: 2px solid rgb(155, 117, 117);
    }
    .back2home:hover,
    .navbar-btn:hover {
        background: #800202;
        background: var(--primary-color);
        border: 2px solid white;
    }

    .navbar ul {
        display: flex;
    }

    .navbar a {
        color: #fff;
        padding: 0.45rem;
        margin: 0 0.25rem;
    }

    .navbar a:hover {
        color: #3d0000;
        color: var(--light-color);
    }

    .navbar .welcome span {
        margin-right: 0.6rem;
    }
    .links {
        text-decoration: none;
        color: rgb(201, 97, 97);
        text-decoration: underline;
    }

    /* about */
    #name {
        justify-content: center;
        text-align: center;
        color: rgb(179, 140, 140);
    }
    #name:hover {
        color: white;
    }
    .searchbox {
        position: fixed;
        top: 100px;
        left: 200px;
    }

    #photo {
        font-size: x-large;
        border: 2px;
        border-radius: 180px;
        border: 2px solid white;
        display: inline-block;
    }
    #childdiv1 {
        margin: 20px;
        display: inline-block;
        color: rgb(196, 180, 180);
        justify-content: center;
        text-align: left;
        font-size: large;
    }
    #childdiv2 {
        width: 350px;
        justify-content: center;
        text-align: center;
        margin: auto;
    }
    #childdiv3 {
        margin-top: 20px;
    }
    #uiuxicon {
        height: 20px;
        width: auto;
    }
}

