        font-size: 1.2rem;
        border: 1px solid #ccc;
    }

    input[type='submit'],
    button {
        font: inherit;
    }

    table th,
    table td {
        padding: 1rem;
        text-align: left;
    }

    table th {
        background: #3d0000;
        background: var(--light-color);
    }

    /* Navbar */
    .navbar {
        display: block;
        text-align: left;
        background-color: #800202;
        background-color: var(--primary-color);
        font-size: smaller;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
    }
    #navbar-icon {
        margin-left: 15px;
        margin-right: 10px;
        font-size: 40px;
    }
    .navbar-content {
        display: flex;
    }
    .navbar-btn {
        color: #fff;
        margin: 0px 0px 0px 7px;
        padding: 2px 20px 0px 20px;
        border-radius: 10px;
        border: 2px solid rgb(155, 117, 117);
    }
    .back2home:hover,
    .navbar-btn:hover {
        background: #800202;
        background: var(--primary-color);
        border: 2px solid white;
    }

    .navbar ul {
        text-align: center;
        justify-content: center;
    }

    .navbar a {
        color: #fff;
        padding: 0.45rem;
        margin: 0 0.25rem;
    }

    .navbar a:hover {
        color: #3d0000;
        color: var(--light-color);
    }

    .navbar .welcome span {
        margin-right: 0.6rem;
    }
    .links {
        text-decoration: none;
        color: rgb(201, 97, 97);
        text-decoration: underline;
    }

    #name {
        justify-content: center;
        text-align: center;
        color: rgb(179, 140, 140);
    }
    #name:hover {
        color: white;
    }
    /* .searchbox {
        position: fixed;
        top: 100px;
        left: 200px;
    } */

    #photo {
        max-width: 200px;
        border: 2px;
        border-radius: 180px;
        border: 2px solid white;
    }
    #childdiv1 {
        margin: auto;
        width: 350px;
        display: inline;
        color: rgb(196, 180, 180);
        justify-content: center;
        text-align: center;
    }
    #childdiv2 {
        width: 350px;
        display: inline;
        margin-top: 30px;
    }
    #childdiv3 {
        margin-top: 20px;
    }
    #uiuxicon {
        height: 20px;
        width: auto;
    }
}


